import React, { useMemo } from "react";
import * as THREE from "three";

export function SelectionBoundingBox({ nodes, isSelected }) {
  const boundingBoxGeometry = useMemo(() => {
    if (!nodes || !isSelected) return null;

    const box = new THREE.Box3();
    let hasGeometry = false;

    Object.entries(nodes).forEach(([key, node]) => {
      if (!node || !node.geometry) return;
      if (key.toLowerCase().includes('line') ||
        key.toLowerCase().includes('edge') ||
        key.toLowerCase().includes('wireframe')) return;
      if (!node.geometry.attributes || !node.geometry.attributes.position) return;
      if (node.type && node.type !== 'Mesh') return;

      const geometry = node.geometry.clone();

      if (node.matrix && !node.matrix.equals(new THREE.Matrix4())) {
        geometry.applyMatrix4(node.matrix);
      }

      geometry.computeBoundingBox();

      if (geometry.boundingBox) {
        if (hasGeometry) {
          box.union(geometry.boundingBox);
        } else {
          box.copy(geometry.boundingBox);
          hasGeometry = true;
        }
      }
    });

    if (!hasGeometry || box.isEmpty()) return null;

    const size = box.getSize(new THREE.Vector3());
    const center = box.getCenter(new THREE.Vector3());

    const boxGeometry = new THREE.BoxGeometry(size.x, size.y, size.z);
    boxGeometry.translate(center.x, center.y, center.z);

    const edgesGeometry = new THREE.EdgesGeometry(boxGeometry);

    return edgesGeometry;
  }, [nodes, isSelected]);

  if (!boundingBoxGeometry) return null;

  return (
    <lineSegments>
      <primitive object={boundingBoxGeometry} attach="geometry" />
      <lineBasicMaterial attach="material" color="#ff0000" linewidth={2} />
    </lineSegments>
  );
}
